.form-field {
  display: flex;
  align-items: center;
  width: 100%;
  height: 50px;
  padding: 10px;
  font-size: 14px;
  line-height: 20px;
  color: #787775;
  border: 1px solid #C2C2C2;
  background-color: transparent;
  box-sizing: border-box;

  .modal__content & {
    height: 46px;
  }

  .form-field__wrapper--sizes & {
    width: calc((100% - 10px) / 3);
  }
}

select.form-field {
  appearance: none;
  background-image: url("../images/arrow-bottom.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-position-x: calc(100% - 10px);
}

textarea.form-field {
  height: 150px;
  resize: none;
}

.form-field__wrapper {
  width: calc((100% - 30px) / 2);
  margin-bottom: 26px;

  @media (max-width: $light-small) {
    width: 100%;
    margin-bottom: 16px;
  }

  &--sizes {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
  }

  .contacts__form & {
    width: calc((100% - 20px) / 2);
  }

  .contacts__form &--full,
  &--full {
    width: 100%;
  }

  .modal__content & {
    width: 100%;
    margin-bottom: 20px;
  }
}

.form-field__label {
  margin-bottom: 3px;
  font-size: 16px;
  line-height: 22px;
  color: #2E2B29;

  & .necessarily {
    color: #E30C0C;
  }

  .form-field__wrapper--sizes & {
    width: 100%;
  }
}
