* {
  outline: none;
  -webkit-tap-highlight-color: transparent;
  scrollbar-color: #2aa8c6 transparent;
  scrollbar-width: thin;
}
*::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

*::-webkit-scrollbar-thumb {
  background-color: #2aa8c6;
  border-radius: 4px;
}

*::-webkit-scrollbar-track {
  background-color: transparent;
  width: 2px;
}

body {
  min-width: $small;
  @include Muller();
  font-size: 16px;
  line-height: 1.5;
  color: #333333;
  background-color: #ffffff;
  overflow-x: hidden;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  color: #2AA8C6;
  text-decoration: none;
  transition-property: text-decoration;
  transition-duration: .2s;
}

