.main-phones {
  @include List-Reset(18px, 24px, 34px);
  position: relative;

  .main-contact--contacts & {
    margin-top: 11px;
    padding-left: 54px;
    margin-bottom: 30px;
  }

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 7px;
    width: 15px;
    height: 15px;
    background-image: url("../images/phone-ico.svg");
    background-position: center;
    background-repeat: no-repeat;

    .main-contact--contacts & {
      top: -2px;
      width: 40px;
      height: 40px;
      border: 1px solid #D2E0E3;
      box-sizing: border-box;
      border-radius: 50%;
    }
  }
}

.main-phones__item {
  &--fax {
    & a {
      &::after {
        content: " (факс)";
      }
    }
  }

  & a {
    line-height: 32px;
    color: #787574;

    .main-contact--contacts & {
      font-size: 18px;
      line-height: 36px;
      color: #000000;
    }
  }
}
