.footer-contact {
  margin-left: auto;
  text-align: right;

  @media (max-width: $medium) {
    margin-right: auto;
    text-align: center;
  }
}

.footer-contact__mail {
  display: inline-block;
  font-size: 17px;
  line-height: 37px;
  letter-spacing: 0.04em;
  color: #C5C5C5;
  transform: translateY(-4px);
}

.footer-contact__request {
  width: 220px;
  color: #2AA8C6;

  &::before {
    display: none;
  }
}
