.modal {
  position: relative;
  width: 580px;
  height: 390px;
  padding-right: 220px;
  box-sizing: border-box;
  overflow: hidden;
  background-color: #FFFFFF;
  background-image: url("../images/modal-bg.jpg");
  background-position: center right;
  background-repeat: no-repeat;
  border-radius: 5px;

  @media (max-width: $light-small) {
    width: 90%;
    max-width: 360px;
    min-height: 390px;
    height: auto;
    padding-right: 0;
    background-image: none;
  }
}

.modal__overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 40;
  display: none;
  background-color: rgba(9, 22, 26, 0.74);

  &.show {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.modal__close {
  position: absolute;
  right: 10px;
  top: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 22px;
  height: 22px;
  padding-right: 0;
  padding-left: 0;
  border: 1px solid #ffffff;
  background-color: transparent;
  border-radius: 50%;
  box-sizing: border-box;
  cursor: pointer;

  @media (max-width: $light-small) {
    border-color: #2aa8c6;
  }

  & svg {
    fill: #FFFFFF;

    @media (max-width: $light-small) {
      fill: #2aa8c6;
    }
  }
}

.modal__content {
  display: flex;
  align-items: flex-start;
  height: 100%;
  padding-right: 40px;
  padding-left: 40px;
  background-color: #FFFFFF;
}

.modal__form-wrapper {
  @media (max-width: $light-small) {
    padding-bottom: 41px;
  }
}

.modal__text {
  font-weight: 500;
  font-size: 22px;
  line-height: 26px;
  letter-spacing: 0.02em;
  color: #2E2B29;
}

.modal__title {
  margin-top: 41px;
  margin-bottom: 30px;
  margin-right: -10px;
}

.modal__success {
  display: none;
  align-self: center;
  margin-right: -10px;
}
