.footer-menu {
  @include List-Reset();

  @media (max-width: $large) {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  @media (max-width: $medium) {
    flex-direction: column;
  }

  & li:not(:last-child) {
    @media (max-width: $light-small) {
      margin-bottom: 5px;
    }
  }

  & a {
    font-size: 17px;
    line-height: 34px;
    color: #888888;

    @media (max-width: $ultra-small) {
      font-size: 16px;
    }
  }
}

.footer-menu__wrapper {
  @media (max-width: $large) {
    order: -1;
    width: 100%;
    margin-bottom: 40px;
  }

  @media (max-width: $medium) {
    order: 1;
    margin-top: 40px;
  }
}
