.page-footer {
  padding-top: 120px;
  padding-bottom: 25px;
  margin-top: auto;
  background-color: #2E2B29;

  @media (max-width: $large) {
    padding-top: 60px;
  }

  @media (max-width: $light-small) {
    padding-top: 52px;
  }
}

.page-footer__inner {
  display: flex;
  align-items: flex-start;
  padding-bottom: 64px;

  @media (max-width: $large) {
    flex-wrap: wrap;
  }

  @media (max-width: $medium) {
    padding-bottom: 40px;
  }
}
