.pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  @include List-Reset(0, 100px, 0);
}

.pagination__item {
  margin-left: 1.5px;
  margin-right: 1.5px;
  font-weight: 500;
  font-size: 18px;
  line-height: 18px;
  color: #929292;

  & span,
  & a {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    border-radius: 3px;
  }

  & span {
    color: #ffffff;
    background-color: #2AA8C6;
  }

  & a {
    color: #929292;
    background-color: #FFFFFF;
    border: 1px solid #E7E7E7;
    box-sizing: border-box;
    transition-property: color, background-color;
    transition-duration: .2s, .2s;

    &:hover {
      color: #ffffff;
      background-color: #2AA8C6;
    }
  }
}
