.promo {
  padding-top: 88px;
  padding-bottom: 20px;
  background-color: #1f95b1;
  background-image: url("../images/promo-bg.jpg");
  background-position: top center;
  background-repeat: no-repeat;

  &--home {
    padding-top: 230px;
    padding-bottom: 220px;
    background-image: url("../images/promo-home-bg.jpg");

    @media (max-width: $medium) {
      padding-bottom: 80px;
    }

    @media (max-width: $ultra-small) {
      padding-bottom: 70px;
      padding-top: 210px;
      background-size: cover;
    }
  }
}

.promo__title {
  font-weight: 700;
  font-size: 30px;
  line-height: 42px;
  letter-spacing: 0.02em;
  color: #FFFFFF;

  .promo--home & {
    margin-top: 0;
    margin-bottom: 46px;
    font-weight: 300;
    font-size: 34px;
    line-height: 48px;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    color: #FFFFFF;

    @media (max-width: $ultra-small) {
      font-size: 18px;
      line-height: 25px;
    }

    @media (max-width: $light-small) {
      margin-bottom: 25px;
    }

    & > span {
      font-weight: 800
    }
  }
}

.promo__calculate {
  transition-property: color, background-color;
  transition-duration: .2s, .2s;

  &:hover {
    background-color: #49C3DF;
    color: #ffffff;
  }
}
