.footer-phones {
  @include List-Reset(17px, 24px, 0);
}

.footer-phones__item {
  &--fax {
    &::after {
      content: "факс";
      display: block;
      margin-top: 5px;
      font-size: 16px;
      line-height: 16px;
      letter-spacing: 0.04em;
      color: #2AA8C6;
    }
  }

  & a {
    font-size: 17px;
    line-height: 27px;
    letter-spacing: 0.04em;
    color: #C5C5C5;
  }
}
