.main-contact__mail {
  display: inline-flex;
  align-items: center;
  line-height: 16px;
  color: #787574;

  .main-contact--contacts & {
    margin-bottom: 14px;
    font-size: 18px;
    line-height: 36px;
    color: #000000;
  }

  &::before {
    content: "";
    display: block;
    width: 18px;
    height: 12px;
    margin-right: 16px;
    background-image: url("../images/mail-ico.svg");
    background-position: center;
    background-repeat: no-repeat;

    .main-contact--contacts & {
      width: 40px;
      height: 40px;
      border: 1px solid #D2E0E3;
      box-sizing: border-box;
      border-radius: 50%;
    }
  }
}

.main-contact__request {
  width: 100%;
  color: #2AA8C6;

  &::before {
    background-image: url("../images/rotate-ico.svg");
  }
}

.main-contact__address {
  position: relative;
  margin-bottom: 38px;
  padding-left: 54px;
  font-size: 18px;
  line-height: 36px;
  color: #000000;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 40px;
    height: 40px;
    border: 1px solid #D2E0E3;
    box-sizing: border-box;
    border-radius: 50%;
    background-image: url("../images/map-ico.svg");
    background-position: center;
    background-repeat: no-repeat;
  }
}

.main-contact__legal-information {
  position: relative;
  padding-left: 54px;
  font-size: 18px;
  line-height: 25px;
  color: #000000;

  &::before {
    content: "";
    position: absolute;
    top: -8px;
    left: 0;
    width: 40px;
    height: 40px;
    border: 1px solid #D2E0E3;
    box-sizing: border-box;
    border-radius: 50%;
    background-image: url("../images/info-ico.svg");
    background-position: center;
    background-repeat: no-repeat;
  }
}
