.about {
  position: relative;
  padding-top: 47px;
  padding-bottom: 50px;
  background-color: #f5f3f0;

  @media (max-width: $light-small) {
    padding-top: 18px;
    padding-bottom: 27px;
  }

  &::after {
    content: "";
    position: absolute;
    bottom: -5px;
    width: 100%;
    height: 5px;
    background-image: url("../images/zigzag-pattern.png");
    background-repeat: repeat-x;
    background-position: left center;
  }
}

.about__inner {
  display: flex;
  align-items: flex-start;

  @media (max-width: $ultra-small) {
    flex-wrap: wrap;
  }
}

.about__content {
  max-width: 680px;
  margin-top: 20px;
  margin-right: 40px;

  @media (max-width: $ultra-small) {
    margin-right: 0;
  }

  @media (max-width: $light-small) {
    margin-top: 0;
  }

  & p {
    font-size: 18px;
    line-height: 31px;
    letter-spacing: 0.02em;
    color: #2E2B29;

    @media (max-width: $ultra-small) {
      font-size: 16px;
      line-height: 22px;
    }
  }
}

.about__thumbnail {
  display: block;
  flex-shrink: 0;
  margin-left: auto;
  margin-right: 40px;
  margin-top: -10px;

  @media (max-width: $ultra-small) {
    margin-top: 0;
    margin-right: auto;
  }
}
