.copyright {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 10px;
  font-size: 15px;
  line-height: 32px;
  color: #828282;
  border-top: 1px solid #4D4D4D;

  @media (max-width: $light-small) {
    flex-direction: column;
  }

  & p {
    margin-top: 0;
    margin-bottom: 0;
  }
}

.copyright__dev {
  display: flex;
  align-items: center;

  & a {
    margin-right: 12px;
    margin-left: 9px;
    color: #7EA32F;
    text-decoration: underline;
    transition-property: text-decoration;
    transition-duration: .2s;

    &:hover {
      text-decoration: none;
    }
  }
}
