.button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 280px;
  height: 60px;
  padding-left: 0;
  padding-right: 0;
  font-size: 18px;
  line-height: 25px;
  letter-spacing: 0.02em;
  color: #2AA8C6;
  background-color: #F5F3F0;
  border-radius: 73px;
  border: none;
  cursor: pointer;
  box-sizing: border-box;

  @media (max-width: $ultra-small) {
    height: 50px;
    font-size: 17px;
    line-height: 24px;
  }
}
