.gray-wrapper {
  position: relative;
  padding-top: 35px;
  padding-bottom: 35px;
  margin-bottom: 80px;
  background-color: #F5F3F0;

  ::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: -5px;
    width: 100%;
    height: 5px;
    background-image: url(../images/zigzag-pattern.png);
    background-repeat: repeat-x;
    background-position: left center;
  }
}
