.content {
  padding-bottom: 83px;

  & img {
    display: block;
    margin-bottom: 30px;
  }

  & p {
    margin-top: 14px;
    margin-bottom: 14px;
    font-size: 18px;
    line-height: 29px;
    color: #000000;
  }

  & h2 {
    margin-top: 65px;
    margin-bottom: 38px;
    font-size: 26px;
    line-height: 36px;
    letter-spacing: 0.02em;
    color: #2AA8C6;
  }
}

.content__inner {
  max-width: 980px;

  .gray-wrapper & {
    max-width: 1010px;
  }
}
