.services {
  @include List-Reset(54px, 21px, 0);
  display: flex;
  align-content: center;
  flex-wrap: wrap;

  @media (max-width: $light-small) {
    flex-direction: column;
    align-content: center;
  }

  @media (max-width: $light-small) {
    margin-top: 26px;
    margin-bottom: 28px;
  }
}

.services__wrapper {
  padding-top: 58px;
  background-color: #ffffff;

  @media (max-width: $light-small) {
    padding-top: 23px;
  }
}

.services__item {
  display: flex;
  align-items: center;
  width: calc((100% - 40px) / 3);
  margin-right: 20px;
  margin-bottom: 60px;

  @media (max-width: $ultra-medium) {
    width: calc(50% - 20px);
  }

  @media (max-width: $light-small) {
    width: auto;
    margin-right: 0;
    margin-bottom: 20px;
  }

  &:nth-child(3n) {
    margin-right: 0;

    @media (max-width: $ultra-medium) {
      margin-right: 20px;
    }

    @media (max-width: $light-small) {
      margin-right: 0;
    }
  }

  &:nth-child(2n) {
    @media (max-width: $ultra-medium) {
      margin-right: 0;
    }
  }
}

.services__thumbnail {
  display: block;
}

.services__thumbnail-wrapper {
  flex-shrink: 0;
  width: 180px;
  height: 180px;
  margin-right: 20px;
  border-radius: 50%;
  background-color: #F5F3F0;

  @media (max-width: $large) {
    width: 140px;
    height: 140px;
  }

  @media (max-width: $ultra-small) and (min-width: $light-small) {
    width: 100px;
    height: 100px;
  }
}

.services__title {
  font-size: 17px;
  line-height: 20px;
  letter-spacing: 0.02em;
  color: #2E2B29;
}
