.page-header {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  z-index: 10;
  padding-top: 9px;
  padding-bottom: 10px;
  background-color: #f5f3f0;

  @media (max-width: $medium) {
    padding-top: 0;
  }

  @media (max-width: $light-small) {
    padding-bottom: 15px;
  }

  &::after {
    content: "";
    position: absolute;
    bottom: -5px;
    width: 100%;
    height: 5px;
    background-image: url("../images/zigzag-pattern.png");
    background-repeat: repeat-x;
    background-position: left center;
  }
}

.page-header__active {
  display: none;

  @media (max-width: $ultra-large) {
    display: flex;
    align-items: center;
    margin-right: auto;
  }

  @media (max-width: $medium) {
    width: 100%;
    margin-right: 0;
    margin-bottom: 14px;
    padding: 13px 10px;
    box-sizing: border-box;
    background-color: #F9F7F6;
  }

  @media (max-width: $light-small) {
    padding-top: 10px;
    padding-bottom: 10px;
  }
}

.page-header__inner {
  display: flex;
  align-items: center;
  justify-content: flex-end;

  @media (max-width: $medium) {
    flex-wrap: wrap;
  }
}

.page-header__phone {
  display: inline-flex;
  align-items: center;
  font-weight: 500;
  font-size: 18px;
  line-height: 36px;
  color: #2E2B29;

  @media (max-width: $medium) {
    margin-left: 10px;
  }

  @media (max-width: $ultra-small) {
    font-size: 16px;
    line-height: 32px;
  }

  &::before {
    content: "";
    width: 15px;
    height: 15px;
    margin-right: 10px;
    background-image: url("../images/phone-ico.svg");
    background-position: center;
    background-repeat: no-repeat;

    @media (max-width: $ultra-small) {
      display: none;
    }
  }
}

.page-header__call {
  margin-left: 22px;
  color: #ffffff;
  background-color: #2AA8C6;

  @media (max-width: $medium) {
    margin-right: 10px;
    margin-left: auto;
  }

  & .request-button__reload::before {
    background-image: url("../images/rotate-light-ico.svg");
  }
}
