.news-item {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: calc((100% - 40px) / 3);
  margin-right: 20px;
  margin-bottom: 40px;
  padding-bottom: 29px;
  background-color: #F9F7F6;

  &::before,
  &::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    height: 2px;
  }

  &::before {
    width: 100%;
    background-color: #CCC0BA;
  }

  &::after {
    z-index: 2;
    width: 83%;
    background-color: #2AA8C6;
  }

  &:nth-child(3n) {
    margin-right: 0;
  }
}

.news-item__thumbnail {
  display: block;
}

.news-item__title {
  margin: 32px 20px 0 20px;
  font-weight: 500;
  font-size: 18px;
  line-height: 25px;
  color: #000000;
  transition-property: color;
  transition-duration: .2s;

  .news-item:hover & {
    color: #2AA8C6;
  }
}

.news-item__description {
  margin: 9px 20px 30px 20px;
  font-size: 16px;
  line-height: 22px;
  color: #9C9896;
}

.news-item__more {
  position: relative;
  display: inline-block;
  margin-top: auto;
  margin-left: 20px;
  font-size: 17px;
  line-height: 24px;
  color: #2AA8C6;
  text-decoration: none;

  &::after {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    bottom: 1px;
    height: 2px;
    background-color: #D7E5E9;
    transition-property: background-color;
    transition-duration: .2s;

    .news-item:hover & {
      background-color: #2AA8C6;
    }
  }
}
