.aside {
  width: 340px;
  flex-shrink: 0;
  background-color: #F7F7F7;
  border-right: 2px solid #F2ECE8;
  box-sizing: border-box;

  @media (max-width: $ultra-large) {
    position: absolute;
    top: 0;
    left: -340px;
    z-index: 20;
    height: 100%;
    transition-property: left;
    transition-duration: .2s;

    &.show {
      left: 0;
    }
  }

  @media (max-width: $light-small) {
    left: initial;
    right: -340px;
    transition-property: right;
    transition-duration: .2s;

    &.show {
      left: initial;
      right: 0;
    }
  }
}

.aside__inner {
  position: relative;
  padding: 50px 50px 60px 50px;
  border-bottom: 1px solid #EDEAE6;
}

.aside__close {
  display: none;

  @media (max-width: $ultra-large) {
    position: absolute;
    top: 15px;
    right: 15px;
    display: block;
    width: 25px;
    height: 25px;
    transform: rotate(45deg);
    border: none;
    background-color: transparent;
    cursor: pointer;

    &::before,
    &::after {
      content: "";
      position: absolute;
      background-color: #A58B7D;
    }

    &::before {
      left: 12px;
      top: 0;
      width: 1px;
      height: 25px;
    }

    &::after {
      left: 0;
      top: 12px;
      width: 100%;
      height: 1px;
    }
  }

  @media (max-width: $light-small) {
    left: 20px;
    top: 22px;
    right: initial;
  }
}
