// Вставка миксина @include mixin_name();

// Шрифт
@mixin Muller($font-weight: 400, $font-style: normal) {
  font-family: "Muller", "Arial", sans-serif;
  font-weight: $font-weight;
  font-style: $font-style;
}

@mixin Montserrat($font-weight: 400, $font-style: normal) {
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: $font-weight;
  font-style: $font-style;
}

// Обнуление списка
@mixin List-Reset($margin-top-value: 0, $margin-bottom-value: 0, $padding-left-value: 0) {
  margin-top: $margin-top-value;
  margin-bottom: $margin-bottom-value;
  padding-left: $padding-left-value;
  list-style: none;
}
