.menu-trigger {
  position: relative;
  display: flex;
  align-items: center;
  width: 24px;
  height: 17px;
  padding-right: 0;
  padding-left: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;

  &::before,
  &::after {
    content: "";
    position: absolute;
    height: 1px;
    background-color: #A58B7D;
  }

  &::before {
    width: 100%;
    top: 0;
    left: 0;
  }

  &::after {
    width: 12px;
    bottom: 0;
    right: 0;
  }
}

.menu-trigger__line {
  height: 1px;
  width: 100%;
  background-color: #A58B7D;
}
