.contacts {
  display: flex;
  padding-bottom: 70px;
}

.contacts__content {
  width: calc((7 / 12) * 100% - 10px);
  margin-right: 20px;
}

.contacts__form {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: space-between;
  margin-top: 10px;
  padding: 25px 30px 50px 30px;
  background-color: #F5F3F0;
  box-sizing: border-box;

  &::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: -5px;
    width: 100%;
    height: 5px;
    background-image: url(../images/zigzag-pattern.png);
    background-repeat: repeat-x;
    background-position: left center;
  }
}

.contacts__form-wrapper {
  width: calc((5 / 12) * 100% - 10px);
}
