.footer-about {
  display: flex;
  align-items: flex-start;
  margin-right: 103px;

  @media (max-width: $ultra-medium) {
    margin-right: 20px;
  }

  @media (max-width: $medium) {
    order: 2;
    padding-top: 40px;
    margin-right: auto;
    margin-left: auto;
    border-top: 1px solid #4B4B4B;
  }
}

.footer-about__logo {
  display: block;
  width: 100px;
  height: 100px;
  margin-right: 25px;
  border-radius: 50%;

  @media (max-width: $ultra-small) {
    width: 80px;
    height: 80px;
  }
}

.footer-about__content {
  & p {
    margin-top: 6px;
    margin-bottom: 20px;
    font-size: 15px;
    line-height: 22px;
    color: #888888;

    @media (max-width: $ultra-small) {
      font-size: 14px;
      line-height: 18px;
    }
  }
}

.footer-about__social {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  background-color: #464646;
  border-radius: 50%;

  @media (max-width: $light-small) {
    display: none;
  }
}
