.custom-order-list {
  padding-left: 0;
  margin-top: 14px;
  margin-bottom: 14px;
  font-size: 18px;
  line-height: 29px;
  color: #000000;
  counter-reset: myCounter;

  & li {
    margin-top: 14px;
    margin-bottom: 14px;
    list-style: none;

    &::before {
      counter-increment: myCounter;
      content: counter(myCounter) '. ';
      color: #2AA8C6;
    }
  }
}


