.main-menu {
  @include List-Reset(0, 147px, 0);
  margin-left: 20px;

  @media (max-width: $light-small) {
    margin-top: 40px;
    margin-bottom: 40px;
  }

  & a {
    font-size: 18px;
    line-height: 54px;
    color: #2E2B29;
    transition-property: color;
    transition-duration: .2s;

    &:hover {
      color: #2AA8C6;
    }
  }
}
