.custom-unorder-list {
  @include List-Reset();

  & li {
    position: relative;
    margin-top: 20px;
    margin-bottom: 20px;
    padding-left: 29px;
    font-size: 18px;
    line-height: 31px;
    letter-spacing: 0.02em;
    color: #000000;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 10px;
      width: 15px;
      height: 12px;
      background-image: url("../images/check-ico.svg");
      background-position: center;
      background-repeat: no-repeat;
    }
  }
}
