.logo {
  display: block;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: 1px solid #E9D7CE;
  box-sizing: border-box;

  .logo__wrapper--header & {
    width: 60px;
    height: 60px;
    margin-right: 10px;

    @media (max-width: $light-small) {
      margin-right: 0;
    }
  }
}

.logo__wrapper {
  display: flex;
  align-items: center;

  &:not(.logo__wrapper--header) {
    margin-bottom: 76px;
    margin-right: -10px;

    @media (max-width: $ultra-large) {
      display: none;
    }
  }

  @media (max-width: $ultra-large) {
    margin-right: 110px;
  }

  @media (max-width: $medium) {
    margin-right: auto;
  }
}

.logo__title {
  margin-left: 10px;
  @include Montserrat(700, normal);
  font-size: 16px;
  line-height: 22px;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: #BC8162;

  .logo__wrapper--header & {
    font-size: 15px;
    line-height: 21px;
    color: #BC8162;
  }
}
