.breadcrumbs {
  display: flex;
  align-items: center;
  @include List-Reset(15px, 51px, 0);
}

.breadcrumbs__item {
  display: flex;
  align-items: center;
  font-size: 15px;
  line-height: 24px;
  color: #787574;

  &:not(:last-child)::after {
    content: "/";
    margin-left: 8px;
    margin-right: 7px;
  }

  & > a {
    color: inherit;
    text-decoration: none;
  }
}
