.calculate__data {
  background-image: url("../images/calculate-bg.jpg");
  background-position: center;
  background-repeat: no-repeat;
}

.calculate__inner {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: space-between;
  width: 670px;
  padding-right: 40px;
  padding-left: 40px;
  background-color: #EFEDEA;
  box-sizing: border-box;

  @media (max-width: $ultra-small) {
    width: auto;
    margin-right: -10px;
    margin-left: -10px;
  }

  @media (max-width: $light-small) {
    padding-right: 10px;
    padding-left: 10px;
  }

  .calculate__data & {
    padding-top: 57px;
    padding-bottom: 54px;

    @media (max-width: $light-small) {
      padding-top: 15px;
      padding-bottom: 35px;
    }
  }

  .calculate__order & {
    padding-top: 75px;
    padding-bottom: 97px;
    background-color: #F5F3F0;

    @media (max-width: $light-small) {
      padding-top: 36px;
      padding-bottom: 60px;
    }
  }
}

.calculate__order {
  background-color: #EFEDEA;
}
