.advantages {
  @include List-Reset(50px, 5px, 0);
  display: flex;
  align-content: flex-start;
  flex-wrap: wrap;

  @media (max-width: $light-small) {
    flex-direction: column;
    align-content: center;
  }

  @media (max-width: $light-small) {
    margin-top: 17px;
    margin-bottom: 29px;
  }
}

.advantages__wrapper {
  position: relative;
  padding-top: 50px;

  @media (max-width: $light-small) {
    padding-top: 16px;
  }

  &::before {
    content: "";
    position: absolute;
    top: -5px;
    width: 100%;
    height: 5px;
    background-image: url("../images/zigzag-pattern-white.png");
    background-repeat: repeat-x;
    background-position: left center;
  }
}

.advantages__item {
  width: calc((100% - 80px) / 3);
  margin-right: 40px;
  margin-bottom: 50px;

  @media (max-width: $medium) {
    display: flex;
    align-items: center;
    width: calc(50% - 40px);
  }

  @media (max-width: $light-small) {
    width: auto;
    margin-right: 0;
    margin-bottom: 2px;
  }

  &:nth-child(3n) {
    margin-right: 0;

    @media (max-width: $medium) {
      margin-right: 40px;
    }

    @media (max-width: $light-small) {
      margin-right: 0;
    }
  }

  &:nth-child(2n) {
    @media (max-width: $medium) {
      margin-right: 0;
    }
  }
}

.advantages__icon-wrapper {
  display: flex;
  align-items: flex-end;
  flex-shrink: 0;
  height: 56px;
  margin-bottom: 27px;

  @media (max-width: $medium) {
    width: 65px;
    margin-bottom: 0;
    margin-right: 15px;
  }
}

.advantages__icon {
  display: block;
}

.advantages__title {
  margin-bottom: 0;
  font-size: 14px;
  line-height: 14px;
  font-weight: 700;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: #2AA8C6;
}

.advantages__description {
  max-width: 284px;
  margin-top: 9px;
  font-size: 18px;
  line-height: 18px;
  color: #2E2B29;
}
