.custom-table {
  min-width: 600px;
  margin-top: 33px;
  margin-bottom: 65px;
  border-collapse: collapse;

  & th {
    padding: 24px 26px 19px 26px;
    font-weight: 500;
    font-size: 14px;
    line-height: 14px;
    letter-spacing: 0.04em;
    text-align: center;
    text-transform: uppercase;
    color: #2AA8C6;
    border-top: 3px solid #EEE3DD;
    border-bottom: 3px solid #EEE3DD;

    &:not(:last-child) {
      border-right: 1px dashed #CCC0BA;
    }
  }

  & tr:not(:last-child) td {
    border-bottom: 1px dashed #CCC0BA;
  }

  & td {
    padding: 16px 26px;
    font-size: 16px;
    line-height: 16px;
    letter-spacing: 0.04em;
    color: #2E2B29;
    text-align: center;
    background-color: #F9F6F4;

    &:not(:last-child) {
      border-right: 1px dashed #CCC0BA;
    }
  }

  &--table-1 {
    & td:last-child {
      text-align: left;
    }
  }

  &--table-2 {
    min-width: 680px;
  }
}
