.section-title {
  font-weight: bold;
  font-size: 30px;
  line-height: 42px;
  letter-spacing: 0.02em;
  color: #2E2B29;

  @media (max-width: $ultra-small) {
    font-size: 26px;
    line-height: 36px;
  }

  .calculate__inner & {
    width: 100%;
    margin-bottom: 41px;
    color: #2AA8C6;

    @media (max-width: $light-small) {
      margin-bottom: 22px;
    }
  }

  .about & {
    margin-bottom: 0;
  }
}
