.request-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 240px;
  height: 46px;
  padding-top: 2px;
  font-size: 17px;
  line-height: 24px;
  letter-spacing: 0.02em;
  color: #2AA8C6;
  border: 2px solid #2AA8C6;
  box-sizing: border-box;
  border-radius: 73px;
  cursor: pointer;

  &.page-header__call {
    @media (max-width: $ultra-small) {
      width: 170px;
      height: 36px;
      font-size: 16px;
      line-height: 22px;
    }
  }

  .modal__content & {
    width: 100%;
  }
}

.request-button__reload {
  position: relative;
  width: 16px;
  height: 16px;
  margin-right: 8px;

  .page-header__call & {
    @media (max-width: $ultra-small) {
      display: none;
    }
  }

  &::before {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    display: block;
    width: 16px;
    height: 19px;
    background-image: url("../images/rotate-ico.svg");
    background-repeat: no-repeat;
    background-position: center;
  }

  &--modal {
    &::before {
      background-image: url(../images/rotate-light-ico.svg);
    }
  }
}
